@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;

  &::-webkit-scrollbar {
    display: none;
  }
}

a {
  color: white;
  text-decoration: none;
}

$bg: rgb(16, 16, 16);

.nav1 {
  height: 70px;
  background-color: $bg;
  display: flex;
  align-items: center;
  padding: 1rem;
  box-sizing: border-box;
  justify-content: space-between;
  > img {
    height: 100%;
    cursor: pointer;
  }

  > div {
    margin-left: 3rem;
    width: 100%;

    > a {
      margin: 1vmax;
      font-weight: 400;
      padding: 9px;
    }
  }
}

.nav2 {
  height: 70px;
  background-color: $bg;
  display: flex;
  align-items: center;
  padding: 1rem;
  box-sizing: border-box;
  justify-content: space-between;

  > button {
    background-color: rgb(255, 24, 24);
    color: white;
    border: none;
    outline: none;
    min-width: fit-content;
    padding: 8px;
    border-radius: 5px;
    margin-left: 20px;
    font-weight: 600;
    cursor: pointer;

    &:hover {
      opacity: 0.9;
    }
  }
}

.header {
  height: 70px;
  background-color: $bg;
  display: flex;
  align-items: center;
  padding: 1rem;
  box-sizing: border-box;
  justify-content: space-between;

