.logo:hover {
  transition: all 0.5s;
  transform: scale(1.15);
}

.navlinks a:hover {
  transition: all 0.2s;
  background-color: rgb(55, 55, 55);
  border-radius: 12px;
}

.navlinks {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.modal .ant-modal-content {
  background-color: rgb(68, 68, 68);
  height: fit-content;
  justify-content: center;
  align-items: center;

  > svg {
    color: white;
  }
}

.modalDiv {
  display: flex;
  flex-direction: column;
  background-color: rgb(39, 39, 39);
  width: 85%;
  padding: 25px;
  border-radius: 5px;
  color: white;
  margin-bottom: 10px;

  > h3 {
    font-size: 25px;
    margin-bottom: 5px;
    margin-top: 5px;
  }

  > input {
    height: 30px;
    padding: 5px 15px;
    background-color: $bg;
    outline: none;
    border-radius: 5px;
    text-decoration: none;
    border: none;
    color: white;
  }

  > input::placeholder {
    color: white;
    opacity: 0.5;
  }

  > button {
    margin-top: 20px;
    padding: 10px;
    width: 50%;
    border-radius: 10px;
    background-color: rgb(255, 37, 37);
    color: white;
    font-size: 15px;
    font-weight: bold;
    border: none;
    margin: 25px auto auto auto;
    cursor: pointer;
  }
}

.modalDivJoin {
  display: flex;
  flex-direction: column;
  background-color: rgb(39, 39, 39);
  padding: 25px;
  border-radius: 5px;
  color: white;
  margin-bottom: 15px;

  > h3 {
    font-size: 20px;
    margin-bottom: 5px;
    margin-top: 5px;
  }

  > input {
    height: 25px;
    padding: 5px 15px;
    background-color: $bg;
    outline: none;
    border-radius: 5px;
    text-decoration: none;
    border: none;
    color: white;
  }

  > input::placeholder {
    color: white;
    opacity: 0.5;
  }

  > button {
    margin-top: 20px;
    padding: 10px;
    width: 50%;
    border-radius: 10px;
    background-color: rgb(255, 37, 37);
    color: white;
    font-size: 15px;
    font-weight: bold;
    border: none;
    margin: 25px auto auto auto;
    cursor: pointer;
  }
}

.footerdiv {
  border-top: 1px solid rgb(48, 48, 48);
  background-color: $bg;
  color: white;
  height: 170px;
  padding: 35px;

  > h4 {
    color: gray;
    font-weight: 400;
    font-size: large;
  }

  > div {
    margin-top: 20px;
    display: grid;
    grid-template-columns: auto auto auto auto;

    > h3 {
      font-weight: 400;
      color: gray;
      font-size: medium;
      margin: 5px;
      cursor: pointer;
      width: fit-content;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.credits {
  padding: 13px 0px;
  color: gray;
  font-weight: 400;
  font-size: medium;
  text-align: center;
  background-color: rgb(29, 29, 29);
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 1px;

  > a {
    color: gray;
    margin: 0px 5px;
    &:hover {
      text-decoration: underline;
    }
  }
}

.srch {
  position: relative;
}

.results {
  position: absolute;
  background-color: aqua;
  min-height: 500px;
  min-width: 450px;
}

.searchDiv {
  display: flex;
  justify-content: right;
  align-items: center;
  position: relative;

  > div {
    position: absolute;
    // left: 156px;
    top: 1px;
    background-color: rgb(0, 0, 0);
    width: 373px;
    min-height: 140px;
    max-height: 400px;
    border-radius: 15px;
    padding: 12px;
    padding-top: 40px;
    box-sizing: border-box;
    color: beige;
    z-index: 35;
    overflow: scroll;
  }
  > input {
    z-index: 36;
    background-color: rgb(64, 64, 64);
    padding: 7px 10px;
    width: 350px;
    color: white;
    letter-spacing: 1px;
    font-weight: 400;
    margin: 0px 0px;
    border-radius: 15px;
    border: none;
    outline: none;

    &::placeholder {
      color: white;
      letter-spacing: 1px;
      font-weight: 400;
    }
  }

  > svg {
    color: white;
    font-size: 18px;
    cursor: pointer;

    &:hover {
      color: rgb(210, 210, 210);
    }
  }
}

// #searchInput:focus + .searchRes {
// visibility: visible;
// }

.singleResDiv {
  display: flex;
  flex-direction: row;
  background-color: rgba(46, 46, 46, 0.729);
  padding: 3px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
}

.srchImg {
  display: flex;
  align-items: center;
  > img {
    height: 85px;
    border-radius: 5px;
  }
}

.srchDetail {
  > p {
    margin-left: 10px;
    font-size: larger;
    display: flex;
    align-items: center;
    font-size: 14px;
    letter-spacing: 1px;
  }
}

.mobile {
  min-height: 100vh;
  min-width: 100vw;
  background-color: $bg;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  > h1 {
    color: white;
    font-weight: 400;
    font-size: 25px;
    letter-spacing: 1px;
  }
  > p {
    color: white;
    letter-spacing: 1px;
    font-size: small;
  }
}

.pers {
  min-height: 70vh;
  background-color: gray;
}

@media only screen and (max-width: 800px) {
  .navlinks {
    display: none;
  }
  // .searchDiv {
  //   // display: none;
  // }
  .nav2 {
    padding: 0;
  }
  .nav1 {
    padding: 0;

    > img {
      height: 40px;
    }
  }

  .footerdiv {
    overflow: hidden;
    > div {
      grid-template-columns: auto auto;
      > h3 {
        font-size: smaller;
      }
    }
  }
  .credits {
    font-size: 15px;

    > svg {
      height: 18px;
      width: 18px;
    }

    > div {
      > svg {
        height: 18px;
        width: 18px;
      }
    }
  }

  .modalDivJoin {
    > h3 {
      font-size: medium;
    }
  }
  .modalDiv {
    > h3 {
      font-size: medium;
    }
  }

  .searchDiv {
    display: flex;
    justify-content: right;
    align-items: center;
    position: relative;

    > input {
      padding: 7px 13px;
      width: 100px;

      &::placeholder {
        font-size: smaller;
      }
    }
  }

  .searchRes {
    left: 0px;
  }
  .searchDiv {
    > div {
      top: 32px;
      background-color: $bg;
      height: 350px;
      width: max-content;
      min-width: 200px;
      max-width: 220px;
    }
  }
  .srchImg {
    display: flex;
    align-items: center;
    > img {
      height: 65px;
      border-radius: 5px;
    }
  }

  .srchDetail {
    > p:nth-child(1) {
      font-size: 15px;
      height: 15px;
      margin: 5px 10px;
      align-items: flex-start;
      overflow: hidden;
    }

    > p {
      width: fit-content;
      margin-left: 10px;
      font-size: 10px;
      letter-spacing: 1px;

      > svg {
        height: 10px;
      }
    }
  }

  .signiii {
    display: none;
  }
}
