$bg: rgb(16, 16, 16);

.headingsMain {
  background-color: $bg;
  padding-top: 5px;
  padding-left: 10px;
  color: white;
}

.banner {
  height: 520px;
  background-color: $bg;
  background-size: cover;
  box-sizing: border-box;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 2rem;

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background: $bg;
    opacity: 0.5;
  }

  h1 {
    color: white;
    font-size: 2.5rem;
    z-index: 10;
  }
  p {
    z-index: 10;
    font-size: 1.1rem;
    color: white;
    font-weight: 100;
    width: 40%;
  }

  div {
    z-index: 10;
    display: flex;
    flex-direction: row;
    // justify-content: center;
    button {
      cursor: pointer;
      padding: 1rem;
      width: 130px;
      background-color: white;
      border: none;
      outline: none;
      z-index: 10;
      margin-right: 1rem;
      border-radius: 5px;
      font-size: 1.2rem;
      align-items: center;
      display: flex;
      justify-content: center;
      transition: all 0.3s;

      &:hover {
        opacity: 0.7;
      }

      svg {
        font-size: 1.5rem;
        margin: 0 5px;
      }
    }
  }
}

.row {
  background-color: $bg;
  padding: 5px;
  position: relative;

  > h2 {
    color: white;
    margin: 0.3rem;
    font-weight: 400;
  }
  > div {
    display: flex;
    overflow-x: auto;
  }
}

.prevBtn {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 20px;
  margin: auto 0;
  height: fit-content;
  z-index: 5;
  background-color: rgba(255, 36, 36, 0.89);
  color: white;
  font-weight: bold;
  font-size: 21px;
  height: 50px;
  border: none;
  width: 50px;
  border-radius: 50%;
  opacity: 65%;
  cursor: pointer;

  &:hover {
    opacity: 100%;
    transform: scale(1.07);
    transition: all 0.2 ease;
  }
}

.nextBtn {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 20px;
  margin: auto 0;
  height: fit-content;
  z-index: 5;
  background-color: rgba(255, 36, 36, 0.89);
  color: white;
  font-weight: bold;
  font-size: 21px;
  height: 50px;
  border: none;
  width: 50px;
  border-radius: 50%;
  opacity: 65%;
  cursor: pointer;

  &:hover {
    opacity: 100%;
    transform: scale(1.07);
    transition: all 0.2 ease;
  }
}

.card {
  width: 350px;
  margin: 0.3rem;
  cursor: pointer;
  transition: transform 0.3s;

  &:hover {
    transform: translateY(-15px);
  }
}

.cardTitle {
  color: white;
  text-align: center;
  font-size: 15px;
  letter-spacing: 1px;
  font-weight: 500;
}

.offline {
  border-top: 1px solid rgb(55, 55, 55);
  background-color: $bg;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  > h1 {
    color: white;
    font-size: 4rem;
    margin: 25px;
  }
  > p {
    color: white;
    font-size: 2rem;
  }
}

.wait {
  -webkit-animation: color-change 2s infinite;
  -moz-animation: color-change 2s infinite;
  -o-animation: color-change 2s infinite;
  -ms-animation: color-change 2s infinite;
  animation: color-change 2s infinite;

  padding: 60px;
  border: 2px solid rgb(107, 107, 107);
  border-radius: 50px;
}

@keyframes color-change {
  0% {
    color: rgb(224, 224, 224);
  }
  20% {
    color: rgb(144, 144, 144);
  }
  40% {
    color: rgb(255, 0, 21);
  }

  80% {
    color: rgb(127, 127, 127);
  }
}

.singleMovieDiv {
  border-top: 1px solid rgb(59, 59, 59);
  min-height: 100vh;
  height: fit-content;
  background-color: $bg;
}

.bannerSingle {
  height: 800px;
  // border-radius: 10px;
  background-color: $bg;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.bannerCont {
  background: linear-gradient(
    90deg,
    #181818 10%,
    hsla(0, 0%, 9%, 0.98) 20%,
    hsla(0, 0%, 9%, 0.97) 25%,
    hsla(0, 0%, 9%, 0.95) 35%,
    hsla(0, 0%, 9%, 0.94) 40%,
    hsla(0, 0%, 9%, 0.92) 45%,
    hsla(0, 0%, 9%, 0.9) 50%,
    hsla(0, 0%, 9%, 0.87) 55%,
    hsla(0, 0%, 9%, 0.82) 60%,
    hsla(0, 0%, 9%, 0.75) 65%,
    hsla(0, 0%, 9%, 0.63) 70%,
    hsla(0, 0%, 9%, 0.45) 75%,
    hsla(0, 0%, 9%, 0.27) 80%,
    hsla(0, 0%, 9%, 0.15) 85%,
    hsla(0, 0%, 9%, 0.08) 90%,
    hsla(0, 0%, 9%, 0.03) 95%,
    hsla(0, 0%, 9%, 0)
  );
  width: 70%;
  height: 800px;
  display: flex;
  align-items: center;
  position: absolute;
  left: 0;
  padding-left: 70px;

  > div {
    > h1 {
      color: white;
      font-size: 55px;
      margin-bottom: 5px;
      max-width: 700px;
      overflow: hidden;
      max-height: 60px;
    }

    > h4 {
      color: rgb(235, 235, 235);
      margin-bottom: 20px;
      font-size: 20px;
    }

    > p {
      color: white;
      width: 650px;
      // word-spacing: 1px;
      letter-spacing: 1px;
      margin-top: 20px;
      font-weight: 400;
    }
  }
}

.iconsData {
  display: flex;
  flex-direction: row;
  margin-top: 15px;

  h5 {
    margin-right: 15px;
    border: 1px solid rgb(163, 163, 163);
    border-radius: 3px;
    padding: 5px;
    color: rgb(178, 178, 178);
    font-weight: 400;
    font-size: 14px;
    width: fit-content;
    display: flex;
    align-items: center;

    > svg {
      font-size: 20px;
    }
  }
}

.sponser {
  background-color: $bg;
  min-height: 160px;
  min-width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: scroll;
}

.detailDiv {
  background-color: $bg;
  padding-top: 20px;
  padding-bottom: 5px;
  padding-left: 50px;
  padding-right: 50px;

  color: white;
  border-top: 1px solid rgb(56, 56, 56);
  border-bottom: 1px solid rgb(56, 56, 56);
}

.moredetails {
  display: grid;
  grid-template-columns: auto auto auto;

  > div {
    margin: 15px;

    > h3 {
      font-size: medium;
      font-weight: 300;
      letter-spacing: 1px;
      color: rgb(184, 184, 184);
    }

    > p {
      font-size: 15px;
      font-weight: 500;
    }
  }
}

.videoDiv {
  height: fit-content;
  background-color: $bg;
  display: grid;
  grid-template-columns: auto auto auto auto;
}

.videoDivM {
  height: 1000px;

  color: white;
  > h1 {
    margin: 15px;
  }
}

.ytvideosDiv {
  display: grid;
  grid-template-columns: auto auto auto;
  margin: 25px 0px;

  > a {
    cursor: default;

    > div {
      background-color: rgb(36, 36, 36);
      border-radius: 5px;
      padding: 9px;
      margin: 15px;
      max-width: 405px;
      display: flex;
      flex-direction: column;

      align-items: center;
      justify-content: center;
      cursor: pointer;

      > h3 {
        margin-top: 5px;
        color: white;
        font-weight: 400;
        font-size: medium;
      }

      > img {
        width: 400px;
        position: relative;
      }
    }
  }
}

.recommendDiv {
  display: grid;
  margin: 35px 0px;
  grid-template-columns: auto auto auto auto;

  > div {
    cursor: pointer;
    position: relative;

    > h2 {
      position: absolute;
      background-color: #181818;
      padding: 3px;
      bottom: 1px;
      font-weight: 600;
      text-align: center;
      font-size: small;
    }

    &:hover {
      transition: all 0.5s;
      transform: scale(1.05);
    }
  }
}

.loadingDiv {
  min-height: 70vh;
  background-color: $bg;
  display: flex;
  justify-content: center;
  // border-top: 1px solid gray;
  align-items: center;

  > div {
    background-color: $bg;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 200px;
    text-align: center;
    color: rgb(255, 24, 24);
  }
}

.genereDiv {
  padding-top: 15px;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
  color: rgb(207, 207, 207);
  background-color: $bg;
  > div {
    cursor: pointer;
    background-color: $bg;
    margin: 5px;
    padding: 5px;
    width: 180px;
    border: 1px solid gray;
    border-radius: 5px;
    text-align: center;

    &:hover {
      transition: all 0.3s ease-in;
      transform: scale(1.1);
    }
  }
}

.genreMainDiv {
  border-top: 1px solid rgb(79, 79, 79);
  min-height: 80vh;
  background-color: $bg;
  padding: 55px;
}

.genCont {
  display: grid;
  grid-template-columns: auto auto auto auto;
}

.singleGen {
  margin: 5px;
  background-color: #181818;
  > img {
    width: 350px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  > h2 {
    font-size: small;
    text-align: center;
  }

  &:hover {
    transition: all 0.3s ease;
    transform: scale(1.04);
  }
}

.genD {
  background-color: $bg;
  padding: 20px;
  > h2 {
    color: white;
    font-weight: 400;
  }
}

