.NotFound {
  background-color: $bg;
  min-height: 70vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  > h1 {
    color: rgb(136, 136, 136);
    font-size: 50px;
  }

  > a {
    margin-top: 15px;
    font-weight: bold;
    color: rgb(222, 222, 222);
    display: flex;
    align-items: center;
    border: 1px solid rgb(255, 44, 44);
    background-color: rgb(255, 30, 30);
    border-radius: 5px;
    padding: 10px;
  }
}

.tvshowsMain {
  border-top: 1px solid rgb(71, 71, 71);
  min-height: 100vh;
  background-color: $bg;
  padding: 10px;
}

@media only screen and (max-width: 800px) {
  .card {
    width: 230px;
    height: 170;
  }
  .cardTitle {
    color: white;
    text-align: center;
    font-size: 12px;
    letter-spacing: 1px;
    font-weight: 500;
  }

  .headingsMain {
    background-color: $bg;
    padding-top: 5px;
    padding-left: 10px;
    font-size: medium;
    color: white;
  }

  .nextBtn,
  .prevBtn {
    display: none;
  }

  .genereDiv {
    padding-top: 15px;
    display: grid;
    grid-template-columns: auto auto auto;
    color: rgb(207, 207, 207);
    background-color: $bg;
    > div {
      cursor: pointer;
      background-color: $bg;
      margin: 5px;
      padding: 5px;
      min-width: fit-content;
      width: 90px;
      border: 1px solid gray;
      border-radius: 5px;
      text-align: center;
      font-size: small;
    }
  }

  .genD {
    background-color: $bg;
    padding: 5px;
    > h2 {
      background-color: $bg;
      padding-top: 5px;
      padding-left: 10px;
      font-size: medium;
      color: white;
    }
  }

  .banner {
    height: 350px;
    background-color: $bg;
    background-size: cover;
    box-sizing: border-box;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 2rem;

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      position: absolute;
      background: $bg;
      opacity: 0.5;
    }

    h1 {
      color: white;
      font-size: 1.5rem;
      z-index: 10;
    }
    p {
      z-index: 10;
      font-size: 0.8rem;
      color: white;
      font-weight: 300;
      width: 60%;
    }

    div {
      z-index: 10;
      display: flex;
      flex-direction: row;
      // justify-content: center;
      button {
        cursor: pointer;
        width: fit-content;
        padding: 10px;
        color: white;
        background-color: rgb(51, 51, 51);
        border: none;
        outline: none;
        z-index: 10;
        margin-right: 1rem;
        border-radius: 5px;
        font-size: 0.9rem;
        align-items: center;
        display: flex;
        justify-content: center;
        transition: all 0.3s;

        &:hover {
          opacity: 0.7;
        }

        svg {
          font-size: 1.5rem;
          margin: 0;
        }
      }
    }
  }
}

@media only screen and (max-width: 600px) {
  .card {
    width: 200px;
  }
}
